import { Observer, observer } from 'mobx-react'
import React from 'react'
import { Container, Dropdown, Icon, Input, Menu } from 'semantic-ui-react'
import styled from 'styled-components'
import { SearchPanelItem, SearchPanelViewModel } from './SearchPanelViewModel'
import { CourseSearchTab } from './SearchTab/CourseSearchTab'
import { ProgramSearchTab } from './SearchTab/ProgramSearchTab'
import { SpecialisationSearchTab } from './SearchTab/SpecialisationSearchTab'


const ResultsSubtextContainer = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 6px;
`

const FoundResults = styled.h4`
  &&& {
    margin: 1rem 0 0.5rem 0.5rem
  }
`

export interface ISearchPanelProps {
  searchPanelViewModel: SearchPanelViewModel
}

const SearchPanel = observer(({ searchPanelViewModel }: ISearchPanelProps) => {
  const getNumResults = () => {
    let numResults = -1
    switch (searchPanelViewModel.activeItem) {
      case (SearchPanelItem.COURSES):
        numResults = searchPanelViewModel.searchResults.courseSearchResults.length
        break
      case (SearchPanelItem.PROGRAMS):
        numResults = searchPanelViewModel.searchResults.programSearchResults.length
        break
      case (SearchPanelItem.SPECIALISATIONS):
        numResults = searchPanelViewModel.searchResults.specialisationSearchResults.length
        break
    }
    return numResults
  }

  return <Observer>
    {
      () => {
        return (
          <React.Fragment>
            <Container>
              <Menu pointing secondary>
                <Menu.Item
                  name={"courses"}
                  active={searchPanelViewModel.activeItem === SearchPanelItem.COURSES}
                  onClick={() => searchPanelViewModel.setActiveItem(SearchPanelItem.COURSES)}
                >
                  {`Courses (${searchPanelViewModel.searchResults.courseSearchResults.length})`}
                </Menu.Item>
                <Menu.Item
                  name={"programs"}
                  active={searchPanelViewModel.activeItem === SearchPanelItem.PROGRAMS}
                  onClick={() => searchPanelViewModel.setActiveItem(SearchPanelItem.PROGRAMS)}
                >
                  {`Programs (${searchPanelViewModel.searchResults.programSearchResults.length})`}
                </Menu.Item>
                <Menu.Item
                  name={"specialisations"}
                  active={searchPanelViewModel.activeItem === SearchPanelItem.SPECIALISATIONS}
                  onClick={() => searchPanelViewModel.setActiveItem(SearchPanelItem.SPECIALISATIONS)}
                >
                  {`Specialisations (${searchPanelViewModel.searchResults.specialisationSearchResults.length})`}
                </Menu.Item>
              </Menu>
              <ResultsSubtextContainer>
                <FoundResults>
                  Found {getNumResults()} result{getNumResults() === 1 ? "" : "s"}
                </FoundResults>

                <span>
                  <span style={{ marginRight: "10px" }}>Results per page</span>
                  <Dropdown
                    text={searchPanelViewModel.resultsPerPage.toString()}
                    selection
                    options={[
                      { text: 10, value: 10 },
                      { text: 20, value: 20 },
                      { text: 30, value: 30 },
                      { text: 50, value: 50 },
                    ]}
                    onChange={(e, data) => { searchPanelViewModel.setResultsPerPage(Number(data.value)) }}
                  />
                </span>
              </ResultsSubtextContainer>
              {
                searchPanelViewModel.activeItem === SearchPanelItem.COURSES
                  ? <CourseSearchTab searchPanelViewModel={searchPanelViewModel} />
                  : <></>
              }
              {
                searchPanelViewModel.activeItem === SearchPanelItem.PROGRAMS
                  ? <ProgramSearchTab searchPanelViewModel={searchPanelViewModel} />
                  : <></>
              }
              {
                searchPanelViewModel.activeItem === SearchPanelItem.SPECIALISATIONS
                  ? <SpecialisationSearchTab searchPanelViewModel={searchPanelViewModel} />
                  : <></>
              }
            </Container>
          </React.Fragment>
        )
      }
    }
  </Observer>

})

export default SearchPanel
