import styled from 'styled-components'
import { Observer, observer } from 'mobx-react';
import React, { useState } from 'react';
import { Button, Input, Menu, Modal } from 'semantic-ui-react';
import { StudyLevel } from '../../interfaces/ModelInterfaces';
import { UserState } from '../../states/UserState';
import SearchPanel from '../SearchPanel/SearchPanel';
import { SearchPanelViewModel } from '../SearchPanel/SearchPanelViewModel';
import OutsideClickHandler from 'react-outside-click-handler';
import { SearchDataState } from '../../states/SearchDataState';

export interface IMenuBarProps {
  userState: UserState
  searchDataState: SearchDataState
}

const MenuBarView = styled(Menu)`
  &&{
    height:50px;
    margin:0;
  }
`

const SearchPanelView = styled.div`
  &&&{
    position:block;
    z-index:420;
    background:white;
    margin-top:11px;
    display: ${props => props.isActive ? "block" : "none"};
    border: 1px solid rgba(34,36,38,.15);
    box-shadow:0 2px 5px 0 rgb(34 36 38 / 15%);
  }
`

const SearchInputItem = styled(Menu.Item)`
  &&&& {
    display:block;
    position: absolute;
    right:0;
    ${props => props.isActive ? "width: calc(min(60vw,1200px))" : "width: 300px"};
    // transition: width 0.2s cubic-bezier(0.000, 0.795, 0.000, 1.000);
    z-index:420;
  }
`

export const MenuBar = observer(({userState, searchDataState}: IMenuBarProps) => {
  const [studyLevelChangeAction, setStudyLevelChangeAction] = useState<(() => void) | undefined>(undefined)
  const [searchPanelActive, setSearchPanelActive] = useState(false);
  const searchPanelViewModel = new SearchPanelViewModel(searchDataState);

  return <Observer>
    {() => {
      return (
        <React.Fragment>
          <MenuBarView>
            <Menu.Item header>Termtangles</Menu.Item>
            <Menu.Item
              name={"Undergraduate"}
              active={userState.studyLevel === StudyLevel.UNDERGRADUATE}
              onClick={
                () => userState.studyLevel !== StudyLevel.UNDERGRADUATE
                  ? setStudyLevelChangeAction(() => () => userState.setStudyLevel(StudyLevel.UNDERGRADUATE))
                  : {}
              }
            />
            <Menu.Item
              name={"Postgraduate"}
              active={userState.studyLevel === StudyLevel.POSTGRADUATE}
              onClick={
                () => userState.studyLevel !== StudyLevel.POSTGRADUATE
                  ? setStudyLevelChangeAction(() => () => userState.setStudyLevel(StudyLevel.POSTGRADUATE))
                  : {}
              }
            />
            <Menu.Item
              name={"Research"}
              active={userState.studyLevel === StudyLevel.RESEARCH}
              onClick={
                () => userState.studyLevel !== StudyLevel.RESEARCH
                  ? setStudyLevelChangeAction(() => () => userState.setStudyLevel(StudyLevel.RESEARCH))
                  : {}
              }
            />
            <Menu.Menu position="right">
              <OutsideClickHandler onOutsideClick={() => setSearchPanelActive(false)}>

                <SearchInputItem isActive={searchPanelActive}>
                  <Input
                    icon="search"
                    iconPosition="left"
                    placeholder="Search"
                    loading={searchPanelViewModel.isLoading}
                    onFocus={() => { setSearchPanelActive(true); }}
                    onChange={(e, data) => searchPanelViewModel.setQueryText(data.value)}
                    value={searchPanelViewModel.queryText}
                  />
                  <SearchPanelView isActive={searchPanelActive}>
                    <SearchPanel searchPanelViewModel={searchPanelViewModel} />
                  </SearchPanelView>
                </SearchInputItem>

              </OutsideClickHandler>
            </Menu.Menu>
          </MenuBarView>

          <Modal
            size='small'
            open={studyLevelChangeAction !== undefined}
            onClose={() => setStudyLevelChangeAction(undefined)}
          >
            <Modal.Header>Study Level Change</Modal.Header>
            <Modal.Content>
              <p>Changing study level will reset all current progress. Do you want to continue?</p>
            </Modal.Content>
            <Modal.Actions>
              <Button negative onClick={() => setStudyLevelChangeAction(undefined)}>
                No
              </Button>
              <Button positive onClick={() => {
                if (studyLevelChangeAction) {
                  studyLevelChangeAction()
                }
                setStudyLevelChangeAction(undefined)
              }}>
                Yes
              </Button>
            </Modal.Actions>
          </Modal>

        </React.Fragment>
      )
    }}
  </Observer>
})
